import React from 'react'

const Link = ({ link }) => {


    const a = link[0].getBoundingClientRect()
    const b = link[1].getBoundingClientRect()

    const x1 = (a.left + a.right) / 2
    const y1 = (a.top + a.bottom) / 2
    const x2 = (b.left + b.right) / 2
    const y2 = (b.top + b.bottom) / 2

    const start = { x: x1, y: y1 }
    const end = { x: x2, y: y2 }

    const ctrl1 = {
        x: ((start.x + end.x )/2 + 20),
        y: ((start.y + end.y) /2 + 20)
    }

    const ctrl2 = {
        x: ((start.x + end.x )/2 - 20),
        y: ((start.y + end.y) /2 - 20)
    }



    return (

        <g>
            {/* <circle
                cx={ctrl1.x} 
                cy={ctrl1.y}
                r={4}
                fill='white'
                stroke='steelblue' 
            />
            <circle
                cx={ctrl2.x} 
                cy={ctrl2.y}
                r={4}
                fill='white'
                stroke='steelblue' 
            /> */}

            <path
                d={`M ${start.x} ${start.y} C ${ctrl1.x} ${ctrl1.y}, ${ctrl2.x} ${ctrl2.y}, ${end.x} ${end.y}`}
                stroke="red"
                fill="transparent"
                strokeDasharray={2}
            />
        </g>


    )
}

export default Link
