import React, { useContext, useState, useRef, useEffect } from 'react'
import { RoomContext } from '../../RoomContext'
import { DraggableCore } from 'react-draggable'
import { project } from '../../utils'


import Lamp from './Lamp'
import Outlet from './Outlet'
import Switch from './Switch'
import Circuit from './Circuit'



function Component({ component, i, isLinking, setIsLinking }) {

    const { __rooms, __componentPropertiesMenu, __linkers, __links } = useContext(RoomContext)
    const [rooms, setRooms] = __rooms
    const [componentPropertiesMenu, setComponentPropertiesMenu] = __componentPropertiesMenu
    const [linkers, setLinkers] = __linkers
    const [links, setLinks] = __links

    const room = rooms.find(({ id }) => id === component.room.id)
    const { anchorPoint, category, node } = component
    let { x, y } = anchorPoint

    const [isDragging, setIsDragging] = useState(false)
    const [isHovered, setIsHovered] = useState(false)
    const [isLinkable, setIsLinkable] = useState(false)
    const [isActiveLinker, setIsActiveLinker] = useState(false)
    const [box, setBox] = useState({})

    const refComponent = useRef();
    const refLinker = useRef()

    useEffect(() => {
        const { current } = refComponent
        setBox(current.getBBox())
        //console.log(current.getBBox())
    }, [component.props.type])

    useEffect(() => {
        if (linkers.length >= 2) {
            setLinks([...links, linkers])
            setLinkers([])
        }
        if (linkers.length === 0) {
            setIsActiveLinker(false)
        }
    }, [linkers])

    if (!component) {
        return null
    }



    if (component.node.nodeName === 'path') {

        const aId = node.id.split('.')[0] + '.' + node.id.split('.')[1]
        const bId = node.id.split('.')[0] + '.' + node.id.split('.')[2]

        const a = room.getPoints().find(({ id }) => id === aId)
        const b = room.getPoints().find(({ id }) => id === bId)

        const newPoint = project(anchorPoint, a, b)

        x = newPoint.point.x
        y = newPoint.point.y

        var angle = (Math.atan2(b.y - a.y, b.x - a.x) * 180 / Math.PI) - 90;
        //console.log(angle)
    }

    const dragging = (e, dnd) => {
        setIsDragging(true)

        const newAnchorPoint = { ...anchorPoint }
        newAnchorPoint.x += dnd.deltaX
        newAnchorPoint.y += dnd.deltaY

        if (component.node.nodeName === 'path') {
            const aId = node.id.split('.')[0] + '.' + node.id.split('.')[1]
            const bId = node.id.split('.')[0] + '.' + node.id.split('.')[2]

            const a = room.getPoints().find(({ id }) => id === aId)
            const b = room.getPoints().find(({ id }) => id === bId)
            const newPoint = project(newAnchorPoint, a, b)

            room.components[i].anchorPoint.x = newPoint.point.x
            room.components[i].anchorPoint.y = newPoint.point.y
        }
        else {
            room.components[i].anchorPoint.x = newAnchorPoint.x
            room.components[i].anchorPoint.y = newAnchorPoint.y
        }
        setRooms([...rooms])
    }

    const dragEnd = e => {
        if (!isDragging) {
            setComponentPropertiesMenu({ open: true, component: component })
        }
        setIsDragging(false)
    }

    const linkerColor = e => {
        if (isLinkable && !isLinking) return 'orange'
        if (isActiveLinker) return '#4BB543'
        if (isLinkable && isLinking) return 'purple'
        else return 'white'
    }

    const switchRender = (category) => {
        switch (category) {
            case 'switch':
                return <Switch props={component.props} angle={angle} />
            case 'outlet':
                return <Outlet props={component.props} angle={angle} />
            case 'lamp':
                return <Lamp props={component.props} angle={angle} />
            default:
                return null
        }
    }

    const handleLinkers = e => {
        setIsLinkable(false)
        isActiveLinker ? setIsActiveLinker(false) : setIsActiveLinker(true)
        isActiveLinker ? setIsLinking(false) : setIsLinking(true)
        setLinkers(prev => [...prev, refLinker.current])
    }

    return (
        <DraggableCore
            handle={'.' + component.category}
            onDrag={dragging}
            onStop={dragEnd}
            disabled={isLinkable}
        >
            <g
                className={category}
                id={component.id}
                transform={`translate(${x} ${y}) scale(1.75) `}
                //stroke='black'
                pointerEvents='all'
                onMouseEnter={() => setIsHovered(true)}
                onMouseLeave={() => setIsHovered(false)}
            >
                <rect
                    className='bbox'
                    transform={`rotate(${angle ? (angle === 0 ? 80 : angle + 90) : 90}) `}
                    x={box.x}
                    y={box.y}
                    width={box.width}
                    height={box.height}
                    fill='steelblue'
                    opacity={0.3}
                    visibility={isHovered || isActiveLinker ? 'visible' : 'hidden'}

                />

                <g
                    ref={refComponent}
                    transform={`rotate(${angle ? (angle === 0 ? 80 : angle + 90) : 90}) `}
                    stroke='black'

                >
                    {switchRender(component.category)}
                </g>
                <circle
                    className='linker'
                    id={'linker.' + component.id}
                    ref={refLinker}
                    cx={0}
                    cy={0}
                    r={3}
                    fill={linkerColor()}
                    stroke='steelblue'
                    visibility={isHovered || isActiveLinker ? 'visible' : 'hidden'}
                    onMouseEnter={() => setIsLinkable(true)}
                    onMouseLeave={() => setIsLinkable(false)}
                    onClick={handleLinkers}


                />
                <g
                    className='circuit'
                    transform={`rotate(${angle ? (angle === 0 ? 80 : angle + 90) : 90}) translate (${0} ${box.y}) `}

                >
                    <Circuit circuit={component.props.circuit} />
                </g>
            </g>


        </DraggableCore>
    )
}

export default Component
