import React, { useState } from 'react'
import { TextField } from '@material-ui/core'

function CircuitChooser({ circuit, handleChange }) {

    const [name, setName] = useState(circuit.name)

    const handleCircuitName = e => {
        setName(e.target.value)
        handleChange(e)
    }

    return (
        <TextField
            id="circuit"
            label="Circuit"
            margin="normal"
            variant='filled'
            onChange={handleCircuitName}
            value={name ? name.toUpperCase() : ''}
        />
    )
}

export default CircuitChooser
