import React, { useContext } from "react";
import { RoomContext } from '../../RoomContext'


export default function Floor({ polygon, isSelected, roomId }) {

    const { setPointer, setQuickMenuPosition, setQuickMenuState } = useContext(RoomContext)

    return <polygon
        id={`${roomId + '.floor'}`}
        points={polygon}
        fill="#ffffaa"
        stroke="steelblue"
        strokeWidth={8}
    />;
}
