import React, { useContext, useState } from "react";
import { RoomContext } from '../../RoomContext'
import { DraggableCore, DraggableData } from "react-draggable";
import { getPath, getPolygon, getAllPoints } from "../../utils";
import Floor from "./Floor";
import Point from "./Point";
import Segment from "./Segment";
import Size from "./Size";
import Door from './Door'
import Outlet from '../ELECTRICAL/Outlet'
import Switch from '../ELECTRICAL/Switch'
import Lamp from '../ELECTRICAL/Lamp'
import Component from './../ELECTRICAL/Component'


function Room({
    isSelected,
    room,
    i
}) {

    const { __rooms, __selectedRoom, __pointer, __quickMenuPosition, __quickMenuState, __node } = useContext(RoomContext)
    const [rooms, setRooms] = __rooms
    const setSelectedRoom = __selectedRoom[1]
    const [pointer, setPointer] = __pointer
    const [quickMenuPosition, setQuickMenuPosition] = __quickMenuPosition
    const [quickMenuState, setQuickMenuState] = __quickMenuState
    const [node, setNode] = __node

    const [isDragging, setIsDragging] = useState(false)
    const [isLinking, setIsLinking] = useState(false)

    const deleteRoom = (e: any) => {
        if (isSelected) {
            if (e.keyCode === 8) {
                setRooms((rooms: any) => rooms.filter((el: any) => el.id !== room.id));
            }
        }
    };

    const roomClicked = e => {
        e.preventDefault()

        let coords = { x: null, y: null }

        if (e.type === 'mouseup' || e.type === 'click') {
            coords.x = e.clientX
            coords.y = e.clientY
            setPointer(coords)
        } else {
            console.log(e)
            console.log(e.originalEvent)
            coords.x = e.changedTouches[0].clientX
            coords.y = e.changedTouches[0].clientY
            setPointer(coords)
        }

        setQuickMenuPosition(coords)
        setQuickMenuState(prev => !prev)
        setNode(e.target)

    }

    const dragging = (e: any, dnd: DraggableData) => {
        e.preventDefault()

        setIsDragging(true)

        const newRooms = [...rooms]
        newRooms[i].x += dnd.deltaX
        newRooms[i].y += dnd.deltaY

        setRooms(newRooms)
    };

    const dragEnded = (e) => {

        if (!isDragging) {
            if (isSelected) { roomClicked(e)}
            return
        }
        
        setIsDragging(false)

        const allPoints = getAllPoints(rooms)

        room.getPoints().forEach(point => {
            allPoints.forEach(otherPoint => {

                let dx = Math.abs(point.absX - otherPoint.absX)
                let dy = Math.abs(point.absY - otherPoint.absY)

                if (point.room !== otherPoint.room) {
                    if (dx <= 16) {
                        rooms[i].points[point.i].x = otherPoint.absX - point.offsetX
                    }
                    if (dy <= 16) {
                        rooms[i].points[point.i].y = otherPoint.absY - point.offsetY
                    }
                }
            })
        })
        setRooms([...rooms])
    }

    return (
        <DraggableCore
            handle=".room"
            cancel=".corner, .segment, .doorCenter, .outlet, .switch, .lamp"
            disabled={!isSelected}
            onStop={dragEnded}
            onDrag={dragging}
        >
            <g
                className={"room"}
                id={room.id}
                transform={`translate(${room.x} ${room.y})`}
                onClick={(e) => {
                    //if (isSelected) { roomClicked(e)}
                    setSelectedRoom(room.id);
                }}
                onKeyDown={deleteRoom}
                tabIndex={-1}
                style={{ outline: 0 }}
            >
                <Floor
                    polygon={getPolygon(room.getPoints())}
                    isSelected={isSelected}
                    roomId={room.id}
                />

                {
                    getPath(room.getPoints()).map((pathPoints, index) => (
                        <Segment
                            key={index}
                            pathPoints={pathPoints}
                            visible={isSelected}
                        />
                    ))
                }

                {
                    room.getPoints().map((point, index) => (
                        <Point
                            key={index}
                            point={point}
                            visible={isSelected}
                        />
                    ))
                }
                {
                    getPath(room.getPoints()).map((pathPoints, index) => (
                        <Size
                            key={index}
                            pathPoints={pathPoints}
                            visible={isSelected}
                        />
                    ))
                }
                {
                    room.doors.map((door, i) => (
                        <Door
                            key={i}
                            door={door}
                            i={i}
                        />
                    ))
                }
                {
                    room.components.map((component, i) => (
                        <Component
                            key={i}
                            component={component}
                            i={i}
                            isLinking={isLinking}
                            setIsLinking={setIsLinking}
                        />
                    ))
                }
                
            </g>
        </DraggableCore>
    );
}

export default Room;

