import React from 'react'

function Circuit(props, angle) {

    const circuit = props.circuit

    return (
        <text
            fontSize='0.6em'
            fontWeight='small'
            transform={`translate(0 -17) )`}
            textAnchor='middle'
        >
            { 
            `${circuit.name}${circuit.number}`.toUpperCase()
            } 
        </text>
    )
}

export default Circuit
