import React from 'react'
import { Grid, Button } from '@material-ui/core'
import { ToggleButton, ToggleButtonGroup } from '@material-ui/lab'

function SwitchProperties({ props, handleChange }) {

    const { way, pole, dimmer, illuminated, type } = props

    return (
            <Grid container direction='column' spacing={1}>
                <Grid item>
                    <Grid
                        container
                        direction='column'
                    >
                        <Grid item xs={12}>
                            <ToggleButtonGroup
                                exclusive
                                aria-label="text alignment"
                                value={type}
                                onChange={handleChange}

                            >
                                <ToggleButton id='type' value={'switch'} selected={type === 'switch'}>Switch</ToggleButton>
                                <ToggleButton id='type' value={'push'} selected={type === 'push'}>Push</ToggleButton>
                                <ToggleButton id='type' value={'inverter'} selected={type === 'inverter'}>Inverter</ToggleButton>
                            </ToggleButtonGroup>
                        </Grid>
                        <Grid item xs={12}>
                            <ToggleButtonGroup
                                exclusive
                                aria-label="text alignment"
                                value={way}
                                onChange={handleChange}

                            >
                                <ToggleButton
                                    value={1}
                                    id='way'
                                    selected={way === 1 && type === 'switch'}
                                    disabled={type !== 'switch'}

                                >
                                    Single throw
                                </ToggleButton>

                                <ToggleButton
                                    value={2}
                                    id='way'
                                    selected={way === 2 && type === 'switch'}
                                    disabled={type !== 'switch'}
                                >
                                    Double throw
                                </ToggleButton>
                            </ToggleButtonGroup>
                        </Grid>
                        <Grid item xs={12}>
                            <ToggleButtonGroup
                                exclusive
                                aria-label="text alignment"
                                value={pole}
                                onChange={handleChange}
                            >
                                <ToggleButton
                                    value={1}
                                    id='pole'
                                    selected={pole === 1 && type === 'switch'}
                                    disabled={type !== 'switch'}

                                >
                                    Single Pole
                                </ToggleButton>

                                <ToggleButton
                                    id='pole'
                                    value={2}
                                    selected={pole === 2 && type === 'switch'}
                                    disabled={type !== 'switch'}

                                >
                                    Double Pole
                                </ToggleButton>

                            </ToggleButtonGroup>
                        </Grid>
                        <Grid item>
                            <ToggleButton
                                id='dimmer'
                                value={dimmer}
                                selected={dimmer}
                                onChange={handleChange}
                            >
                                Dimmer
                            </ToggleButton>
                        </Grid>
                        <Grid item>
                            <ToggleButton
                                id='illuminated'
                                value={illuminated}
                                selected={illuminated}
                                onChange={handleChange}
                            >
                                Illuminated
                            </ToggleButton>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>

    )
}

export default SwitchProperties
