import React from 'react'
import { Grid } from '@material-ui/core'
import { ToggleButton, ToggleButtonGroup } from '@material-ui/lab'

function OutletProperties({ props, handleChange }) {

    const { shutter, type, grounding } = props

    return (
        <Grid container direction='column' spacing={1}>
            <Grid item>
                <Grid
                    container
                    direction='column'
                >
                    <Grid item xs={12}>
                        <ToggleButtonGroup
                            exclusive
                            aria-label="text alignment"
                            value={type}
                            onChange={handleChange}

                        >
                            <ToggleButton id='type' value={1} selected={type === 1}>ONE</ToggleButton>
                            <ToggleButton id='type' value={2} selected={type === 2}>TWO</ToggleButton>
                            <ToggleButton id='type' value={3} selected={type === 3}>THREE</ToggleButton>
                            <ToggleButton id='type' value={4} selected={type === 4}>FOOR</ToggleButton>
                            <ToggleButton id='type' value={5} selected={type === 5}>FIVE</ToggleButton>
                        </ToggleButtonGroup>
                    </Grid>
                </Grid>
            </Grid>
            <Grid item>
                <Grid container justify="center" spacing={0}>

                    <Grid item xs={6} style={{ textAlign: "center" }} >
                        <ToggleButton
                            id='grounding'
                            value={grounding}
                            selected={grounding}
                            onChange={handleChange}
                        >
                            Grounding
                        </ToggleButton>
                    </Grid>
                    <Grid item xs={6} style={{ textAlign: "center" }} >
                        <ToggleButton
                            id='shutter'
                            value={shutter}
                            selected={shutter}
                            onChange={handleChange}
                        >
                            Shutter</ToggleButton>
                    </Grid>
                </Grid>
            </Grid>
        </Grid>
    )
}

export default OutletProperties
