import React, { useState } from "react";
import Plan from "./components/BUILDING/Plan";
import "./App.css";
import QuickMenu from './components/UI/QuickMenu'
import { RoomContext } from './RoomContext'
import ComponentPropertiesMenu from "./components/UI/ComponentPropertiesMenu";

function App() {
    const __rooms = useState([])
    const __components = useState([])
    const __links = useState([])
    const __quickMenuPosition = useState(null)
    const __quickMenuState = useState(false)
    const __selectedRoom = useState(null)
    const __selectedPathPoints = useState(null)
    const __pointer = useState(null)
    const __doors = useState([])
    const __node = useState(null)
    const __outletPropertiesMenu = useState({ open: false })
    const __switchPropertiesMenu = useState({ open: false })
    const __componentPropertiesMenu = useState({ open: false })
    const __linkers = useState([])

    const CTX = {
        __rooms,
        __components,
        __links,
        __quickMenuPosition,
        __quickMenuState,
        __selectedRoom,
        __selectedPathPoints,
        __pointer,
        __doors,
        __node,
        __outletPropertiesMenu,
        __switchPropertiesMenu,
        __componentPropertiesMenu,
        __linkers
    }

    return (
        <div
            className="App"
            style={{ overscrollBehavior: "none" }}
            onContextMenu={e => e.preventDefault()}
        >
            <RoomContext.Provider value={CTX}>
                <Plan />
                <QuickMenu />
                {/* <OutletPropertiesMenu />
                <SwitchPropertiesMenu /> */}
                <ComponentPropertiesMenu />
            </RoomContext.Provider>
        </div>
    );
}

export default App;
