import React, { useContext } from 'react'
import { Dialog, DialogTitle, Grid, Button, Typography } from '@material-ui/core'
import { RoomContext } from '../../RoomContext'
import SwitchProperties from './SwitchProperties'
import OutletProperties from './OutletProperties'
import LampProperties from './LampProperties'
import CircuitChooser from './CircuitChooser'
import { getAllElementsOnCircuit } from './../../utils'

function ComponentPropertiesMenu() {

    const { __componentPropertiesMenu, __rooms } = useContext(RoomContext)

    const [componentPropertiesMenu, setComponentPropertiesMenu] = __componentPropertiesMenu
    const [rooms, setRooms] = __rooms

    const { open, component } = componentPropertiesMenu

    if (!component) {
        return null
    }
    console.log(component.id)

    const roomIndex = rooms.findIndex(({ id }) => id === component.room.id)
    const componentIndex = rooms[roomIndex].components.findIndex(({ id }) => id === component.id)
    console.log(componentIndex)

    const handleClose = () => {
        setComponentPropertiesMenu({})
    }

    const handleChange = (e) => {

        let newValue = e.currentTarget.value

        if (newValue === 'true') newValue = false
        else if (newValue === 'false') newValue = true
        else if (newValue === '') newValue = ''
        else if (!isNaN(newValue)) newValue = parseInt(newValue)

        const property = e.currentTarget.id
        if (property === 'circuit') {
            console.log(rooms[roomIndex])
            rooms[roomIndex].components[componentIndex].props[property].name = newValue
            rooms[roomIndex].components[componentIndex].props[property].number = getAllElementsOnCircuit(rooms, newValue)
        }
        else {
            rooms[roomIndex].components[componentIndex].props[property] = newValue
        }
        setRooms([...rooms])

    }

    const renderSwitch = (category) => {
        switch (category) {
            case 'switch':
                return <SwitchProperties props={component.props} handleChange={handleChange} />
            case 'outlet':
                return <OutletProperties props={component.props} handleChange={handleChange} />
            case 'lamp':
                return <LampProperties props={component.props} handleChange={handleChange} />
            default:
                break;
        }
    }

    return (
        <Dialog open={open} onClose={handleClose}>
            <DialogTitle>Set component properties</DialogTitle>
            <Typography>
                id : {component.id}
            </Typography>
            <CircuitChooser circuit={component.props.circuit} handleChange={handleChange} />

            {
                renderSwitch(component.category)
            }
            <Grid item>
                <Button onClick={() => setComponentPropertiesMenu({ open: false })} variant="contained" color="primary" fullWidth>
                    Save
                    </Button>
            </Grid>

        </Dialog>
    )
}

export default ComponentPropertiesMenu
