import React, { useContext } from 'react'
import { Menu, MenuItem } from '@material-ui/core'
import { getPointCoords, getAllPoints } from './../../utils'
import { RoomContext } from './../../RoomContext'

const QuickMenu = () => {

    const {
        __rooms,
        __components,
        __quickMenuPosition,
        __quickMenuState,
        __selectedPathPoints,
        __pointer,
        __componentPropertiesMenu,
        __node
    } = useContext(RoomContext)

    const [rooms, setRooms] = __rooms
    const [components, setComponents] = __components
    const [quickMenuPosition, setQuickMenuPosition] = __quickMenuPosition
    const [quickMenuState, setQuickMenuState] = __quickMenuState
    const [selectedPathPoints] = __selectedPathPoints
    const [pointer] = __pointer
    const [componentPropertiesMenu, setComponentPropertiesMenu] = __componentPropertiesMenu
    const [node, setNode] = __node

    if (!node) return null

    const handleClose = () => {
        setQuickMenuState(false)
        setQuickMenuPosition(null)
    }

    const addCorner = e => {
        //e.persist()

        const [a] = selectedPathPoints
        const roomIndex = rooms.findIndex(room => room.id === a.room)

        const x = pointer.x - rooms[roomIndex].x
        const y = pointer.y - rooms[roomIndex].y

        const newPoint = getPointCoords([x, y], selectedPathPoints)

        getAllPoints(rooms).forEach(point => {
            //console.log(newPoint)
            if (Math.abs(point.absX - (newPoint.x + rooms[roomIndex].x)) < 16) {
                newPoint.x = point.absX - rooms[roomIndex].x
            }
            if (Math.abs(point.absY - (newPoint.y + rooms[roomIndex].y)) < 16) {
                newPoint.y = point.absY - rooms[roomIndex].y
            }
        })

        const point1 = { ...newPoint }
        const point2 = { ...newPoint }

        rooms[roomIndex].points.splice(a.i + 1, 0, point1, point2)
        setRooms([...rooms])

        handleClose()
    }

    const addDoor = () => {

        const [a, b] = selectedPathPoints

        const roomIndex = rooms.findIndex(room => room.id === a.room)

        const x = pointer.x - rooms[roomIndex].x
        const y = pointer.y - rooms[roomIndex].y

        const doorCenter = getPointCoords([x, y], selectedPathPoints)
        const doorId = rooms[roomIndex].id + '.door.' + rooms[roomIndex].doors.length


        const pointsIds = { a: a.id, b: b.id }

        const door = { pointsIds, doorCenter, doorId }

        rooms[roomIndex].doors.push(door)

        setRooms([...rooms])

        handleClose()
    }

    const addComponent = (e) => {

        const room = rooms.find(({ id }) => id === node.id.split('.')[0])

        const x = pointer.x - room.x
        const y = pointer.y - room.y

        const anchorPoint = { x, y }
        const index = rooms.find(({ id }) => id === room.id).components.filter(({ category }) => category === e.target.id).length

        const category = e.target.id
        const id = room.id + `.${category}.` + index

        let props

        switch (e.target.id) {
            case 'lamp':
                props = {circuit : {name : '', number: ''}}
                break;
            case 'outlet':
                props = {
                    type: 1,
                    grounding: true,
                    shutter: true,
                    circuit : {name : '', number: ''}
                }
                break;
            case 'switch':
                props = {
                    way: 1,
                    pole: 1,
                    dimmer: false,
                    illuminated: false,
                    type: 'switch',
                    circuit : {name : '', number: ''}
                }
                break;

            default:
                break;
        }

        const component = {
            anchorPoint,
            id,
            category,
            room,
            node,
            props
        }

        room.components.push(component)

        setRooms([...rooms])
        handleClose()
    }

    return (
        <Menu
            id="quickMenu"
            transitionDuration={{ enter: 0, exit: 0 }}
            anchorReference="anchorPosition"
            anchorPosition={
                quickMenuPosition !== null
                    ? { top: quickMenuPosition.y, left: quickMenuPosition.x }
                    : undefined
            }
            open={quickMenuState}
            onClose={handleClose}
        >
            {node.nodeName === 'path' ? <MenuItem onClick={addCorner}>add corner</MenuItem> : null}
            {node.nodeName === 'path' ? <MenuItem onClick={addDoor}>add door</MenuItem> : null}
            <MenuItem onClick={addComponent} id='outlet'>add outlet</MenuItem>
            <MenuItem onClick={addComponent} id='switch'>add switch</MenuItem>
            {node.nodeName === 'polygon' ? <MenuItem onClick={addComponent} id='lamp'>add lamp</MenuItem> : null}
        </Menu>
    )
}

export default QuickMenu
